@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@300&family=Roboto&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

img {
  image-rendering: optimizeSpeed;
  image-rendering: -moz-crisp-edges;
  image-rendering: -o-crisp-edges;
  image-rendering: pixelated;
  image-rendering: optimize-contrast;
  -ms-interpolation-mode: nearest-neighbor;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

/*
* Location for the settings form
*/
#settings-container {
  margin-top: 6rem;
  width: 100%;
  max-width: 55em;
  margin: 0 auto;
}

/*
* Centering the text on the settings-page
*/
#settings-help {
  text-align: center;
}

/*
* Location for the player name and grid size components
*/
.settingsDiv {
  margin: auto;
  width: 100%;
  max-width: 55em;
  padding: 10px;
  text-align: center;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(25em, 1fr));
  grid-gap: 1em;
  gap: 1em;
}

/*
* Style for the labels in the Settings-component
*/
.settingsLabel {
  margin-left: 10px;
  font-size: 1.25rem;
}

/*
* Style for the input fields in the Settings-components
*/
.settingsInput {
  height: 1.5rem;
  width: 3rem;
  font-size: 1.25rem;
  margin-right: 10px;
  border-radius: 2rem;
  border: solid 1px var(--dark);
  padding: 5px;
  padding-left: 8px;
  padding-right: 8px;
}

/*
* Style for the input fields in the Settings-component that should be long
*/
.settingsInput.long {
  min-width: 6rem;
  max-width: 12rem;
  width: 100%;
}

/*
* Location of the component that contains ship amounts
*/
#ship-container {
  margin: auto;
  max-width: 40em;
  padding: 10px;
  text-align: center;
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-row-gap: 1rem;
  row-gap: 1rem;
}

/*
* Location of the component that contains ship amounts on a smaller screen
*/
@media screen and (max-width: 45em) {
  #ship-container {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
  }
}

/*
* Location of the component for destroyer amounts
*/
#destroyerDiv {
  grid-column: 1 / span 2;
}

/*
* Location for the submit button
*/
#submitButtonDiv {
  margin: 0 auto;
  text-align: center;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  width: 17rem;
  -webkit-align-items: center;
          align-items: center;
}

/*
* Style for the buttons on the settings-page
*/
.settings-button {
  margin-top: 1.5em;
  width: -webkit-max-content;
  width: max-content;
  padding: 25px;
  height: 5rem;
  font-size: 24px;
  background-color: var(--light);
  border: 1px solid var(--primary);
  font-family: "Montserrat";
  color: #000000;
  border-radius: 2rem;
}

/*
* Style for the submit button, when the mouse hovers over it
*/
.settings-button:hover {
  background-color: var(--light2);
  cursor: pointer;
}

/*
* Style for the error message
*/
#error {
  text-align: center;
  font-size: 24px;
  color: var(--error);
}

.Setup {
  display: -webkit-flex;
  display: flex;
}

.ShipPool {
  height: 100vh;
  width: 20%;
  padding: 30px;
  padding-top: 0px;
  border-right: 3px solid var(--primary);
  color: var(--primary);

  text-align: center;
}

#setShip {
  margin-top: 4em;
  font-family: "Montserrat";
}

.ship-container {
  height: 50%;
  overflow: auto;
}

.confirm-button {
  font-size: 1.2em;
  margin: 10px;
  padding: 10px;
  background-color: var(--light);
  border-radius: 2rem;
  border: 1px solid var(--primary);
  font-family: "Montserrat";
  color: #000000;

  transition: opacity 100ms;
}

.confirm-button:disabled {
  opacity: 0.4;
}

.confirm-button:hover:disabled {
  cursor: not-allowed;
}

.confirm-button:hover:enabled {
  background-color: var(--light2);
  cursor: pointer;
}

.board-area {
  height: 100vh;
  width: 80%;

  color: gray;
}

.DnDBoard {
  background-size: 320px;
  -webkit-animation: ocean 120s linear infinite;
          animation: ocean 120s linear infinite;
}

.DnDCell {
  display: grid;
  place-content: center;
  width: 3rem;
  height: 3rem;
  text-align: center;
  border: 1px solid rgba(255, 255, 255, 0.2);
  /*box-sizing because then borders wont make the cell bigger*/
  box-sizing: border-box;

  background-color: var(--cell-bg);
  transition: background-color 250ms;
}

.DnDCell.outer {
  pointer-events: none;
  border: 1px solid transparent;

  background-color: var(--bg);
}

.DnDCell.Denied {
  background-color: var(--denied-drop);
}

.DnDShip {
  width: -webkit-max-content;
  width: max-content;
  height: -webkit-max-content;
  height: max-content;
  min-width: 3rem;
  min-height: 3rem;

  -webkit-animation: boat-waves 5s infinite ease-in-out;

          animation: boat-waves 5s infinite ease-in-out;
}

.DnDShip .ShipPart {
  width: 3rem;
  height: 3rem;
}

.DnDShip .ShipPart img {
  pointer-events: none;
  width: 100%;
  height: 100%;
}

.DnDShip .ShipPart img.horizontal {
  -webkit-transform: rotate(-90deg);
          transform: rotate(-90deg);
}

/*
* Positions win screen and blurs background
*/
#winScreenContainer {
  position: absolute;
  top: 0;
  height: 100%;
  width: 100%;
  display: grid;
  place-content: center;
  -webkit-backdrop-filter: blur(7px);
          backdrop-filter: blur(7px);
  z-index: 10;
}

/*
* Win screen style
*/
.WinScreen {
  width: 100%;
  height: 100%;

  max-width: 40em;
  max-height: 30em;

  padding: 1.5em;

  border-radius: 1em;

  background-color: rgb(240, 240, 240);

  box-shadow: 0 1em 4em rgba(0, 0, 0, 0.25);
}

/*
* Style for the buttons in the win screen
*/
.home-button.ws {
  width: 18rem;
  font-size: 24px;
  padding: 15px;
}

/*
* Style for the title on the win screen
*/
.title.ws {
  margin-top: 1.25em;
}

/*
* Locates buttons on the win screen
*/
#wsButtonContainer {
  text-align: center;
  display: grid;
  grid-template-columns: 1fr;
  grid-gap: 2em;
  margin: 1em;
  margin-top: 4em;
}

/*
* Style for the button that closes the win screen
*/
#close-button {
  float: right;
  width: 2.25rem;
  height: 2.25rem;
  font-size: 25px;
  background-color: var(--light);
  border: 1px solid var(--primary);
  font-family: "Montserrat";
  color: var(--dark);
  border-radius: 100%;
}

/*
* Style for the close-button when the mouse hovers over it
*/
#close-button:hover {
  background-color: var(--light2);
  cursor: pointer;
}

/*
* Style for the symbol inside of the close-button
*/
#close-button img {
  display: -webkit-flex;
  display: flex;
  width: 100%;
}

@-webkit-keyframes message-anim {
  from {
    -webkit-transform: scale(1.5);
            transform: scale(1.5);
  }
  to {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
}

@keyframes message-anim {
  from {
    -webkit-transform: scale(1.5);
            transform: scale(1.5);
  }
  to {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
}

/*
* Style for the message that tells the player if he has hit or sunk a ship
*/
#message {
  height: 0;
  margin: 0 auto;
  width: 25em;
  margin-top: 2em;
  text-align: center;
  font-family: "Montserrat";
}

#message > h2 {
  -webkit-animation: message-anim 150ms both;
          animation: message-anim 150ms both;
}

/* Container for both boards */
.BattleshipBoards {
  display: grid;
  grid-auto-flow: column;
  place-content: center;
  grid-gap: 3em;
  gap: 3em;

  height: 100vh;
}

.BoardContainer {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  text-align: center;
}

.BoardContainer .PlayerName {
  color: gray;
}

.Board {
  display: grid;
  height: -webkit-max-content;
  height: max-content;

  transition: opacity 500ms;

  -ms-interpolation-mode: nearest-neighbor;

      image-rendering: -moz-crisp-edges;

      image-rendering: pixelated;

  background-size: 320px;
  -webkit-animation: ocean 120s linear infinite;
          animation: ocean 120s linear infinite;
}

/* Current player's own board */
.Board.own {
  opacity: 0.7;
  cursor: not-allowed;
}

.BoardCell {
  display: grid;
  place-content: center;

  width: 3rem;
  height: 3rem;

  box-sizing: border-box;

  border: 1px solid rgba(255, 255, 255, 0.2);
  transition: background-color 400ms;
}

.BoardCell:not(.outer) {
  background-color: var(--cell-bg);
}

.Board:not(.own) .BoardCell:not(.clicked):hover {
  background-color: rgba(255, 255, 255, 0.2);

  transition: background-color 0ms;
}

.BoardCell.clicked {
  cursor: not-allowed !important;
}

.BoardCell.clicked::before {
  font-size: 2rem;
  content: "✕";
  display: -webkit-flex;
  display: flex;
  place-content: center;

  width: 100%;
  height: 100%;

  color: white;

  z-index: 2;
}

.BoardCell.hit::before {
  font-weight: bold;
  color: rgb(255, 0, 0);
}

.BoardCell.clicked:hover {
  -webkit-filter: none !important;
          filter: none !important;
}

.Board:not(.own) .BoardCell.hit .Ship {
  opacity: 0.6;
}

.BoardCell .Ship {
  position: absolute;
  width: 3rem;
  height: 3rem;

  -webkit-animation: boat-waves 5s infinite ease-in-out;

          animation: boat-waves 5s infinite ease-in-out;
}

.BoardCell .Ship img {
  width: 3rem;
  height: 3rem;
}

.BoardCell .Ship img.horizontal {
  -webkit-transform: rotate(-90deg);
          transform: rotate(-90deg);
}

/* Cells in enemy player's board */
.Board:not(.own) .BoardCell {
  cursor: pointer;
}

/* Cells that aren't part of the play area */
.BoardCell.outer {
  pointer-events: none;
  border: 1px solid transparent;

  background-color: var(--bg);
}

.BoardCell.outer .OuterText {
  display: grid;
  place-content: center;

  position: absolute;

  width: 3rem;
  height: 3rem;

  color: gray;
}

/* Outer shell to align the content to the middle */
.ChangeScreen {
  display: grid;
  place-content: center;

  position: fixed;

  width: 100%;
  height: 100%;
}

/* Content of the change screen */
#turn {
  text-align: center;
  font-family: "Montserrat";
}

