/*
* Variables for the UI's colours
*/
:root {
  --primary: #00bac8;
  --light: #e0f7f8;
  --light2: #b2eaec;
  --bg: #ebf4f5;
  --dark: #095e5d;
  --error: #c80d00;
  --denied-drop: rgba(192, 47, 66, 0.9);

  --cell-bg: rgba(0, 187, 200, 0.8);
}

/*
* Background colour
*/
body,
#home-bg {
  background-color: #ebf4f5;
  background-color: var(--bg);
  font-family: "Roboto";
}

/*
* Style for the titles
*/
.title {
  text-align: center;
  margin-top: 1em;
  font-family: "Montserrat";
  font-size: 55px;
  color: #00bac8;
  color: var(--primary);
}

/*
* Style for subtitles
*/
.sub-title {
  text-align: center;
  margin-top: 2.25em;
  font-family: "Montserrat";
  font-size: 24px;
  color: #00bac8;
  color: var(--primary);
}

/*
* Location for the home page buttons
*/
#home-button-container {
  margin: auto;
  margin-top: 6%;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  width: 50%;
  padding: 10px;
  grid-gap: 4em;
  gap: 4em;
}

/*
* Style for the home page buttons
*/
.home-button {
  width: 14rem;
  height: 5rem;
  font-size: 30px;
  background-color: #e0f7f8;
  background-color: var(--light);
  border: 1px solid #00bac8;
  border: 1px solid var(--primary);
  font-family: "Montserrat";
  color: #000000;
  border-radius: 2rem;
}

/*
* Style for the back button
*/
.back-button {
  position: absolute;
  top: 1em;
  left: 1em;
  width: 3rem;
  height: 3rem;
  font-size: 30px;
  background-color: #e0f7f8;
  background-color: var(--light);
  border: 1px solid #00bac8;
  border: 1px solid var(--primary);
  font-family: "Montserrat";
  color: #095e5d;
  color: var(--dark);
  border-radius: 100%;
}

/*
* Style for the symbol for the back button
*/
.back-button img {
  display: -webkit-flex;
  display: flex;
  width: 100%;
  height: 100%;
  -webkit-transform: scale(0.8);
          transform: scale(0.8);
}

/*
* Style for the back button in the play phase
*/
.back-button.play {
  font-size: 20px;
  width: 10rem;
  border-radius: 1.42rem;
}

/*
* Style for the buttons, when the mouse hovers over it
*/
.home-button:hover,
.back-button:hover {
