  background-color: var(--light2);
  cursor: pointer;
}

/*
* Location for the help-page content
*/
#help-div {
  margin-bottom: 5rem;
}

/*
* Style for the text on the Help-page
*/
.help-text {
  text-align: justify;
  text-justify: inter-word;
  padding-left: 5rem;
  padding-right: 5rem;
  font-size: 18px;
  max-width: 50rem;
  margin: 0 auto;
}

/*
* Colour for the link in the help-page
*/
#body-link {
  color: #00bac8;
  color: var(--primary);
}

/*
* Style for the link when hovered
*/
