}

/*
* Location for the credits on the Credit-page
*/
#credits-container {
  margin: auto;
  margin-top: 6%;
  text-align: center;
  grid-gap: 4em;
  gap: 4em;
}

/*
* Style for the text on the Credits-page
*/
.credit-name {
  font-family: "Montserrat";
  font-size: 24px;
  color: #095e5d;
  color: var(--dark);
}

/*
* Animation used to move the ocean background
*/
@-webkit-keyframes ocean {
  from {
    background-position: 0px 0px;
  }
  to {
    background-position: 320px 320px;
  }
}
@keyframes ocean {
  from {
    background-position: 0px 0px;
  }
  to {
    background-position: 320px 320px;
  }
}

/*
* Animation used to make the ships shift a bit like in waves
*/
@-webkit-keyframes boat-waves {
  0% {
    -webkit-transform: translateY(0px);
            transform: translateY(0px);
  }
  50% {
    -webkit-transform: translateY(3px);
            transform: translateY(3px);
  }
  100% {
    -webkit-transform: translateY(0px);
            transform: translateY(0px);
  }
}
@keyframes boat-waves {
  0% {
    -webkit-transform: translateY(0px);
            transform: translateY(0px);
  }
  50% {
    -webkit-transform: translateY(3px);
            transform: translateY(3px);
  }
  100% {
    -webkit-transform: translateY(0px);
            transform: translateY(0px);
  }
}

